.sidebar{
    display:flex;
    flex-direction: column;
    flex:0.35
}
.sidebarHeader{
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgray;
    min-width: 10vw;

}
.sidebarHeaderRight > .MuiSvgIcon-root{
    margin: 2vw;
    font-size: 24px !important;

}
.sidebarSearch{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
}
.sidebarSearchContainer{
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}
.sidebarSearchContainer >.MuiSvgIcon-root{
    color: gray;
    padding: 10px;
}
.sidebarSearchContainer > input{
    border: none;
    outline-width: 0;
    margin-left: 10px;
}
.sidebarChats{
    flex:1;
    background-color: white;
    overflow: scroll;
}
 